<template>
  <!--法律工具页面-->
  <div>
    <Header></Header>
    <div class="row">
      <!--side-bar-->
      <div class="flex-shrink-0 p-3 bg-white col-4" style="width: 280px;min-height: 600px;">
        <a-menu
            style="width: 256px"
            :default-selected-keys="['1']"
            :open-keys.sync="openKeys"
            mode="inline"
            @click="handleClick"
        >
          <a-sub-menu key="sub1" @titleClick="titleClick">
            <span slot="title"><span>常见问题</span></span>
            <a-menu-item-group key="g1">
              <a-menu-item key="1">
                婚姻家庭
              </a-menu-item>
              <a-menu-item key="2">
                遗产继承
              </a-menu-item>
              <a-menu-item key="3">
                村宅基地
              </a-menu-item>
              <a-menu-item key="4">
                债务债权
              </a-menu-item>
            </a-menu-item-group>
          </a-sub-menu>
          <a-sub-menu key="sub2" @titleClick="titleClick">
            <span slot="title"><span>文书模板</span></span>
            <a-menu-item key="5">
              民事答辩状
            </a-menu-item>
            <a-menu-item key="6">
              民事起诉状
            </a-menu-item>
            <a-menu-item key="7">
              上诉状
            </a-menu-item>
            <a-menu-item key="8">
              反诉状
            </a-menu-item>
            <a-menu-item key="9">
              公民授权委托书
            </a-menu-item>
            <a-menu-item key="10">
              法人或其他组织授权委托书
            </a-menu-item>
          </a-sub-menu>
          <a-sub-menu key="sub3">
            <span slot="title"><span>赔偿计算</span></span>
            <a-menu-item key="11">
              在线查看
            </a-menu-item>
          </a-sub-menu>
        </a-menu>
      </div>
      <div class="col-8">
        <router-view></router-view>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
  import Header from "@/components/Header";
  import Footer from "@/components/Footer";

  export default {
    name: 'LegalTools',
    components: {Header, Footer},
    data() {
      return {
        openKeys: ['sub1'],
      };
    },
    watch: {
      openKeys(val) {
        // console.log('openKeys', val);
      },
    },
    methods: {
      gotoPage(name, selector) {
        let flag = false
        if (this.$route.name !== name) {
          this.$router.push({
            name: name
          })
          flag = true
        }
        if (flag) {
          this.$nextTick(function () {
            if (selector) {
              this.$scrollTo(selector, 400, {offset: -100})
            }
          })
        } else {
          if (selector) {
            this.$scrollTo(selector, 400, {offset: -100})
          }
        }
      },
      handleClick(e) {
        // console.log('click', e);
        switch (e.key) {
          case '1':
            this.gotoPage('faq', '#hunyin');
            break;
          case '2':
            this.gotoPage('faq', '#yichan');
            break;
          case '3':
            this.gotoPage('faq', '#cunzhai');
            break;
          case '4':
            this.gotoPage('faq', '#zhaiwu');
            break;
          case '5':
            this.gotoPage('instrumentTemplate', '#msdb');
            break;
          case '6':
            this.gotoPage('instrumentTemplate', '#msqs');
            break;
          case '7':
            this.gotoPage('instrumentTemplate', '#ssz');
            break;
          case '8':
            this.gotoPage('instrumentTemplate', '#fsz');
            break;
          case '9':
            this.gotoPage('instrumentTemplate', '#gmsqwt');
            break;
          case '10':
            this.gotoPage('instrumentTemplate', '#frsqwt');
            break;
          case '11':
            this.gotoPage('compensationCalculation');
            break;
        }
      },
      titleClick(e) {
        // console.log('titleClick', e);
        switch (e.key) {
          case 'sub1':
            this.gotoPage('faq', '#hunyin');
            break;
          case 'sub2':
            this.gotoPage('instrumentTemplate', '#msdb');
            break;
          case 'sub3':
            this.gotoPage('compensationCalculation');
            break;
        }
      },
    },
    mounted() {
      this.gotoPage('faq', '#hunyin');
    }
  }
</script>

<style scoped>

  .row {
    margin-top: 6%;
  }


</style>